.login {
    background-color: rgb(0 0 0 / 54%);
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
  }
  .login .form{
    min-width: 50%;
    min-height: 400px;
    background-image: linear-gradient( 150deg, #eccae6 30%, #b0bddd 70%, #c6d5e4 100%);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    display: flex;
    border-radius: 25px;
    border: 5px solid #fff;
    box-shadow: -2px -3px #000;
  }
  .login .form .close-btn{
    font-size: 1em;
    padding: 5px;
    background: #000A;
    color: white;
    margin-top: 5px;
    position: absolute;
    right: 0;
    top:0;
    width: 50px;
    margin-right: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 25px;

  }